














import { Action } from 'vuex-class';
import { Component, Ref, Vue } from 'vue-property-decorator';
import { IInputInfo } from '@/types/interface';
import * as commonTypes from '../../types/common';
import inputFormPanel from '../../components/InputFormPanel.vue';

@Component({
  components: {
    inputFormPanel,
  },
})

export default class ActivitySetProfile extends Vue {
  @Ref('inputForm') readonly inputForm!: inputFormPanel;
  @Action('saveTempProfile') saveTempProfile!: (info: IInputInfo) => Promise<void>;

  inputList = [commonTypes.INPUT_ID, commonTypes.INPUT_NAME, commonTypes.INPUT_PHONE,
    commonTypes.INPUT_EMAIL, commonTypes.INPUT_FULL_ADDRESS];

  close() {
    this.$router.go(-2);
  }

  next() {
    const info: IInputInfo | undefined = this.inputForm.getInput();
    if (info) {
      this.saveTempProfile(info)
        .then(() => {
          this.$router.push('/activity/main');
        });
    }
  }
}
